import { createGlobalStyle } from "styled-components";
import { normalize } from "styled-normalize";
import { color } from "./color";

//   `normalize`  css  .
const GlobalStyle = createGlobalStyle`
  ${normalize}
  #root,
  html,
  body {
    ${color}
    overflow: auto;
    height: 100vh;
  }

  * {
    box-sizing: border-box;
  }
  
 
`;

export default GlobalStyle;
