// TODO : callback return Type   
export const testAsync = (callback: any, delayTime?: number) =>
  new Promise((resolve, rejects) => {
    setTimeout(() => {
      try {
        const callbackRet = callback?.();
        resolve(callbackRet);
      } catch (e) {
        rejects("callback Func implements failed");
      }
    }, delayTime ?? 500);
  });
